.icon, .rotate {
  width: 20px;
  float: left;
}

.rotate {
  animation: rotate 2s infinite linear;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  24% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  49% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  74% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  99% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
